.App {
    text-align: center;
  }
  
  .chat-container {
    height: 400px;
    border: 1px solid #ccc;
    margin: 50px;
    padding: 20px;
    overflow-y: auto;
  }
  
  form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  input {
    width: 300px;
    height: 30px;
    margin-right: 10px;
  }
  
  .user {
    text-align: right;
    color: blue;
  }
  
  .bot {
    text-align: left;
    color: red;
  }
  